import React, { Component } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar'
import { OverlayTrigger, Tooltip, Dropdown } from 'react-bootstrap';

class CaptionDropdown extends Component {
    constructor(props) {
        super(props)
        console.log("props.item.addTags TagDropdown", props.item.caption)
        this.state = {
            isOpen: false,
            caption: props.item.caption
        }
    }

    onClickToggle = event => {
        const { isOpen } = this.state
        this.setState({ isOpen: !isOpen })
    }
    onClickSubmit = (uIndex) => event => {
        this.props.onSubmitCaption(uIndex, this.state.caption)
        this.setState({ isOpen: false })
    }
    onClickToReset = (uIndex) => event => {
        this.setState({ caption: "" }, () => this.props.onResetCaption(uIndex, ""))
    }
    onChangeText = (uIndex) => event => {
        this.setState({ caption: event.target.value })
    }
    componentWillReceiveProps(nextProps) {
        if (this.state.caption != nextProps.item.caption) this.setState({ caption: nextProps.item.caption })
    }

    render() {
        const { isOpen, caption } = this.state
        const { uIndex, item } = this.props
        return <> <Dropdown className={`position-relative`} id={`caption${uIndex}`} show={isOpen} onToggle={this.onClickToggle}>
            <Dropdown.Toggle variant='btn' className="d-flex input-group p-0 arrow_disabled">
                <input type="text" className="form-control form-control-sm fs-10" style={{ minHeight: 30 }} placeholder="Enter Caption..." aria-label="Enter Caption..." aria-describedby="mediaCaption" readOnly={true} value={item.caption} />
                <button type="button" className="input-group-text svg-icon svg-icon-7 me-0 px-2 text-gray-800" id="mediaCaption" onClick={this.onClickToggle}>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                        <path fill="none" d="M0 0h24v24H0z" />
                        <path d="M16.757 3l-2 2H5v14h14V9.243l2-2V20a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V4a1 1 0 0 1 1-1h12.757zm3.728-.9L21.9 3.516l-9.192 9.192-1.412.003-.002-1.417L20.485 2.1z" fill="currentColor" />
                    </svg>
                </button>
            </Dropdown.Toggle>
            <Dropdown.Menu className="popover p-2 bg-light position-absolute start-0 top-0 w-100 rounded-1 box-shadow">
                <div className="d-flex align-items-center justify-content-between mb-2">
                    <div className="fs-9 fw-semibold text-gray-800">Add Caption</div>
                    <OverlayTrigger placement="top" overlay={<Tooltip>Reset</Tooltip>}>
                        <button type="button" className="btn btn-sm btn-icon btn-active-light btn-active-color-primary p-0 h-24px w-24px" onClick={this.onClickToReset(uIndex)}>
                            <span className="svg-icon svg-icon-7 me-0">
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                    <path fill="none" d="M0 0h24v24H0z" />
                                    <path d="M5.828 7l2.536 2.536L6.95 10.95 2 6l4.95-4.95 1.414 1.414L5.828 5H13a8 8 0 1 1 0 16H4v-2h9a6 6 0 1 0 0-12H5.828z" fill="currentColor" />
                                </svg>
                            </span>
                        </button>
                    </OverlayTrigger>
                </div>
                <textarea className="form-control form-control-sm textarea-min-row-4" value={this.state.caption} onChange={this.onChangeText(uIndex)} />
                <div className="w-100 d-flex justify-content-end pt-2">
                    <button type="submit" className="btn btn-xs btn-primary" onClick={this.onClickSubmit(uIndex)}>Done</button>
                </div>
            </Dropdown.Menu>
        </Dropdown></>
    }
}

export default CaptionDropdown