const mapDispatchToProps = dispatch => {
    return {
        createAssets: (data, callback) => dispatch(createAssets(data, callback)),
    }
}

const mapStateToProps = state => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(UploadedAssets);